import { STATIC_API_CNT } from "@/config/site"

type PropsType = {
  pageID?: string
  category?: string
  all?: boolean
  cnt?: number
}

export default async function getReportsStatic(params?: PropsType) {
  const { pageID = '1', category = null, all = false, cnt = null } = params || {}

  // const url = new URL(
  //   `${process.env.NEXT_PUBLIC_BASE_URL}/rcms-api/7/insights/list`,
  // )
  // url.searchParams.append('pageID', String(pageID))
  
  // const response = await fetch(url,
  //   {
  //     headers: {
  //       'X-RCMS-API-ACCESS-TOKEN': process.env.INSIGHTS_ACCESS_TOKEN!,
  //     }
  //   }
  // )
  const url = new URL(
    `https://tourism.g.kuroco.app/rcms-api/2/reports/list`,
  )
  url.searchParams.append('pageID', String(pageID))
  const slug = category || ''
  if (slug) url.searchParams.append('contents_type', slug)
  if (!all && cnt) {
    url.searchParams.append('cnt', cnt.toString())
  } else if (!all) {
    url.searchParams.append('cnt', STATIC_API_CNT)
  }
  
  const response = await fetch(url)
  return await response.json()
}